import { h, JSX } from '@stencil/core';
import { navContentStore } from '../../state/nav-content-store';
import { isString } from '../../utility/helper';

export const MainDrawerContent = ({
  activeRoutingKey,
  locale,
  breakpoint,
  backButtonText,
  onClickBack,
  receivedContent
}: {
  activeRoutingKey: string;
  locale: string;
  breakpoint: number;
  backButtonText: string;
  onClickBack: () => void;
  receivedContent: boolean;
}): JSX.Element => (
  <div class="drawer-content">
    <div class="main-drawer-header">
      {isString(backButtonText) ? (
        <phn-back-button
          class="back-button"
          onClick={onClickBack}
          text={backButtonText}
          analyticsId={navContentStore.state.menu.id}
          activeRoutingKey={activeRoutingKey}
        />
      ) : null}
    </div>

    <phn-router
      activeRoutingKey={activeRoutingKey}
      breakpoint={breakpoint}
      locale={locale}
      receivedContent={receivedContent}
    />
  </div>
);
