import { h, JSX } from '@stencil/core';
import { navContentStore } from '../../state/nav-content-store';
import { isString } from '../../utility/helper';

export const SideDrawerContent = ({
  activeRoutingKey,
  locale,
  backButtonText,
  onClickBack
}: {
  activeRoutingKey: string;
  locale: string;
  backButtonText: string;
  onClickBack: () => void;
}): JSX.Element => {
  return (
    <div class="drawer-content">
      <span class="first-focus" tabIndex={-1} aria-hidden="true" />

      <div class="side-drawer-header">
        {isString(backButtonText) ? (
          <phn-back-button
            class="back-button"
            onClick={onClickBack}
            text={backButtonText}
            analyticsId={navContentStore.state.menu.id}
            activeRoutingKey={activeRoutingKey}
          />
        ) : null}
      </div>

      <phn-side-drawer activeRoutingKey={activeRoutingKey} locale={locale} />
    </div>
  );
};
