@import '../../scss';

$drawer-width: (
  'm':  p-px-to-rem(336px),
  'l':  p-px-to-rem(405px),
  'xxl':  p-px-to-rem(606px),
  'default': p-px-to-rem(330px)
);
$drawer-paddings: (
  'm':  p-px-to-rem(36px),
  'l':  p-px-to-rem(48px),
  'xxl':  p-px-to-rem(84px),
  'default': p-px-to-rem(28px)
);

:host {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: fit-content;
  z-index: $phn-z-index-mega-flyout + 1;

  .close-button {
    $button-width: 48px;
    $button-margin: 28px;
    position: absolute;
    right: p-px-to-rem(-($button-width + $button-margin));
    z-index: $phn-z-index-mega-flyout + 1;
    width: p-px-to-rem($button-width);
    height: p-px-to-rem($button-width);
    margin-top: p-px-to-rem($button-margin);
    cursor: pointer;
  }

  .drawer {
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    background-color: $pds-theme-light-background-base;
    height: 100vh;

    &#side-drawer {
      z-index: $phn-z-index-mega-flyout + 2;
      float: left;
      width: calc(map-get($drawer-width, 'default') - calc(map-get($drawer-paddings, 'default') +  map-get($drawer-paddings, 'default')));
      padding-left: map-get($drawer-paddings, 'default');
      padding-right: map-get($drawer-paddings, 'default');

      @include pds-media-query-min('m') {
        width: calc(map-get($drawer-width, 'm') - calc(map-get($drawer-paddings, 'm') +  map-get($drawer-paddings, 'm')));
        padding-left: map-get($drawer-paddings, 'm');
        padding-right: map-get($drawer-paddings, 'm');
      }
      @include pds-media-query-min('l') {
        width: calc(map-get($drawer-width, 'l') - calc(map-get($drawer-paddings, 'l') +  map-get($drawer-paddings, 'l')));
        padding-left: map-get($drawer-paddings, 'l');
        padding-right: map-get($drawer-paddings, 'l');
      }
      @include pds-media-query-min('xxl') {
        width: calc(map-get($drawer-width, 'xxl') - calc(map-get($drawer-paddings, 'xxl') +  map-get($drawer-paddings, 'xxl')));
        padding-left: map-get($drawer-paddings, 'xxl');
        padding-right: map-get($drawer-paddings, 'xxl');
      }

      phn-side-drawer {
        padding-bottom: 6rem;
      }

      .side-drawer-header {
        box-sizing: border-box;
        height: p-px-to-rem(92px);
        padding-top: p-px-to-rem(42px);

        @include pds-media-query-min('l') {
          height: p-px-to-rem(96px);
        }

        @include pds-media-query-min('xxl') {
          height: p-px-to-rem(128px);
          padding-top: p-px-to-rem(60px);
        }

        phn-back-button {
          margin-left: p-px-to-rem(-8px); // to make it look like the icon starts with the side-drawer-item
          height: p-px-to-rem(24px);
        }
      }
    }

    &#main-drawer {
      z-index: $phn-z-index-mega-flyout + 1;
      float: right;
      border-radius: 0 4px 4px 0;
      background-color: $main-drawer-background-color;
      box-sizing: border-box;
      position: static;
      width: map-get($drawer-width, 'default');
      padding-left: map-get($drawer-paddings, 'default');
      padding-right: map-get($drawer-paddings, 'default');

      @include pds-media-query-min('m') {
        width: map-get($drawer-width, 'm');
        padding-left: map-get($drawer-paddings, 'm');
        padding-right: map-get($drawer-paddings, 'm');
      }
      @include pds-media-query-min('l') {
        width: map-get($drawer-width, 'l');
        padding-left: map-get($drawer-paddings, 'l');
        padding-right: map-get($drawer-paddings, 'l');
      }
      @include pds-media-query-min('xxl') {
        width: map-get($drawer-width, 'xxl');
        padding-left: map-get($drawer-paddings, 'xxl');
        padding-right: map-get($drawer-paddings, 'xxl');
      }

      phn-router {
        padding-bottom: 6rem;
      }

      .main-drawer-header {
        box-sizing: border-box;
        height: p-px-to-rem(66px);
        padding-top: p-px-to-rem(40px);
        margin-bottom: p-px-to-rem(26px);

        @include pds-media-query-min('l') {
          margin-bottom: p-px-to-rem(30px);
        }

        @include pds-media-query-min('xxl') {
          height: p-px-to-rem(84px);
          margin-bottom: p-px-to-rem(44px);
          padding-top: p-px-to-rem(40px);
        }

        phn-back-button {
          // to make it look like the icon aligns with the left border of the main drawer
          margin-left: p-px-to-rem(-8px);
          height: p-px-to-rem(24px);
        }
      }

      .bottom-gradient {
        @include drawer-gradient(rgba(255, 255, 255, 0), $main-drawer-background-color);
        width: map-get($drawer-width, 'default');
        margin-left: - map-get($drawer-paddings, 'default');
        border-radius: 0 0 4px 0;

        @include pds-media-query-min('m') {
          width: map-get($drawer-width, 'm');
          margin-left: - map-get($drawer-paddings, 'm')
        }

        @include pds-media-query-min('l') {
          width: map-get($drawer-width, 'l');
          margin-left: - map-get($drawer-paddings, 'l')
        }

        @include pds-media-query-min('xxl') {
          width: map-get($drawer-width, 'xxl');
          margin-left: - map-get($drawer-paddings, 'xxl')
        }
      }
    }
  }
}
